





























import Vue, { PropType } from "vue";
export default Vue.extend({
	model: {
		prop: "result"
	},
	props: {
		result: { type: String as PropType<string> }
	},
	data() {
		return {
			selectResult: "sunrinpay"
		};
	},
	watch: {
		selectResult(value: string) {
			this.$emit("input", value);
		}
	}
});
