



























import Vue from "vue";
import QRcodeVue from "@/components/QRcode.vue";
import NumberCounterVue from "@/components/NumberCounter.vue";
export default Vue.extend({
	components: {
		QRcode: QRcodeVue,
		NumberCounter: NumberCounterVue
	},
	data() {
		return {
			time: 180
		};
	},
	mounted() {
		setInterval(() => {
			this.time--;
		}, 1000);
	},
	computed: {
		getSecond(): number {
			return this.time % 60;
		},
		getMinute(): number {
			return Math.floor(this.time / 60);
		}
	}
});
